import axios from 'axios';
import { ref } from 'vue';
const data = getData();
export default {
  data() {
    return {
      data
    };
  }
};
function getData() {
  let data = ref(JSON);
  axios.get(process.env.VUE_APP_API_URL + '/json?page=0&size=5', {
    Headers: {
      "Access-Control-Allow-Origin": "*"
    }
  }).then(res => {
    data.value = res.data;
    console.log(res);
  }).catch(err => {
    console.log(err);
    data = err;
  }).finally();
  return data;
}